.job-entry {
    position: relative;
}

.job-entry > h3 {
    position: absolute;
    left: -24px;
    top: -11px;
}

.job-entry .workpanel-marker {
    margin-bottom: 20px;
}

.workpanel-marker {
    background-color: black;
    display: block;
    border-radius: .2rem;
    width: 40px;
    height: 40px;
    margin-left: 55px;
}

.workpanel-marker-facebook {
	background: -webkit-gradient(
		linear
		, left bottom, left top
		, from(white)
		, color-stop(50%, white)
		, color-stop(50%, #3b5998)
		, to(#3b5998)
	);
	background: -webkit-linear-gradient(
		bottom
		, white 0%
		, white 50%
		, #3b5998 50%
		, #3b5998 100%
	);
	background: -o-linear-gradient(
		bottom
		, white 0%
		, white 50%
		, #3b5998 50%
		, #3b5998 100%
	);
	background: linear-gradient(
		0deg
		, white 0%
		, white 50%
		, #3b5998 50%
		, #3b5998 100%
	);
}

.workpanel-marker-google {
	background: -webkit-gradient(
		linear
		, left bottom, left top
		, from(#4285f4)
		, color-stop(25%, #4285f4)
		, color-stop(25%, #34a853)
		, color-stop(50%, #34a853)
		, color-stop(50%, #fbbc05)
		, color-stop(75%, #fbbc05)
		, color-stop(75%, #ea4335)
		, to(#ea4335)
	);
	background: -webkit-linear-gradient(
		bottom
		, #4285f4 0%
		, #4285f4 25%
		, #34a853 25%
		, #34a853 50%
		, #fbbc05 50%
		, #fbbc05 75%
		, #ea4335 75%
		, #ea4335 100%
	);
	background: -o-linear-gradient(
		bottom
		, #4285f4 0%
		, #4285f4 25%
		, #34a853 25%
		, #34a853 50%
		, #fbbc05 50%
		, #fbbc05 75%
		, #ea4335 75%
		, #ea4335 100%
	);
	background: linear-gradient(
		0deg
		, #4285f4 0%
		, #4285f4 25%
		, #34a853 25%
		, #34a853 50%
		, #fbbc05 50%
		, #fbbc05 75%
		, #ea4335 75%
		, #ea4335 100%
	);
}

